body {
  text-align: center;
  background-color: #f5f5f5;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  background-color: #333;
  padding: 15px;
}

header h1 {
  margin: 1rem;
}

#login {
  width: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px #ccc;
}

#login h2 {
  margin-top: 0;
}

#form {
  flex-direction: column;
  display: flex;
}

label, input, select {
  margin: 10px 0;
}

label {
  font-weight: bold;
}

input[type="text"], input[type="password"], select {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px;
}

#create-account-button, #log-in-button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
}

#create-account-button:hover, #log-in-button:hover {
  background-color: #444;
}

#logged-in-users {
  text-align: center;
  width: 40%;
  align-items: center;
  margin-top: 20px;
  margin-left: 62%;
  padding: 0;
  list-style: none;
  display: flex;
}

#logged-in-users li {
  text-align: end;
  width: 70%;
  background-color: #ff0;
  margin-bottom: 10px;
  margin-left: 50px;
  padding: 10px;
}

.post-status-button, .logout-button {
  text-align: center;
  color: #fff;
  background-color: #333;
  margin: 10px;
  padding: 10px;
}

.status-input {
  width: 30%;
  height: 30px;
  margin-left: 35%;
}

